
import { defineComponent, ref, watch } from 'vue';

export default defineComponent({
  name: 'CCCheckbox',
  emits: ['update:value', 'change'],
  props: {
    value: {
      type: Boolean,
      default: false,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
    label: {
      type: String,
      default: '',
    },
  },
  setup(props, { emit }) {
    const val = ref(props.value);
    const label = ref(props.label);
    watch(
      () => props.value,
      (v) => {
        val.value = v;
      },
      {
        deep: true,
      }
    );
    return {
      val,
      label,
      onChange() {
        const v = val.value;
        emit('update:value', v);
        emit('change', v);
      },
    };
  },
});
