
import { closeCommand } from './const';
import { CmdData } from './const';
import { ref, defineComponent, PropType } from 'vue';
export default defineComponent({
  name: 'item',
  props: {
    data: {
      type: Object as PropType<CmdData>,
      required: true,
    },
  },
  setup(props, ctx) {
    return {
      onMouseEnter() {},
      onMouseLeave() {},
      onClick() {
        const { cb, url } = props.data;
        if (url) {
          window.open(url);
        } else {
          if (cb) {
            cb();
          }
        }
        closeCommand();
      },
    };
  },
});
