
import { createPopper } from '@popperjs/core';
import { debounce, DebouncedFunc } from 'lodash';
import { TinyEmitter } from 'tiny-emitter';
import { defineComponent, inject, nextTick, onMounted, provide, ref, toRaw, watch } from 'vue';
import { config } from './const';
import { uiElement } from '../element';
export default defineComponent({
  name: 'CCProp',
  emits: ['changeExpand', 'update:expand', 'slide'],
  props: {
    name: {
      type: String,
    },
    tooltip: {
      type: String,
      default: '',
    },
    align: {
      type: String,
      default: 'center',
    },
    /**
     * 属性是否展开，主要是针对 Object/Array 设计的
     */
    expand: {
      type: Boolean,
      default: false,
    },
    /**
     * 是否显示icon
     * false:不显示，并且不会占用icon的空间，主要事针对Vec2/Vec3/Vec4设计的
     */
    icon: {
      type: Boolean,
      default: true,
    },
    /**
     * 是否显示箭头，无论是否显示都会占用空间，主要是针对 Object/Array 设计的
     */
    arrow: {
      type: Boolean,
      default: false,
    },
    /**
     * slide操作的步进
     */
    step: {
      type: Number,
      default: 1,
    },
    /**
     * 在属性名字是否可以滑动，主要是方便用来触发value的step操作
     *
     * @example
     * ```ts
     * // 子组件需要如下对接slide行为
     * const emitter: TinyEmitter = inject<TinyEmitter>('emitter', new TinyEmitter());
     * emitter.on('slide', (n:number) => {
     *    console.log(n);
     * });
     * ```
     */
    slide: {
      type: Boolean,
      default: false,
    },
    /**
     * 缩进
     */
    indent: {
      type: Number,
      default: 0,
    },
    /**
     * 当为true时，prop.name背景将会带颜色
     */
    hint: {
      type: Boolean,
      default: false,
    },

    /**
     * 属性名字的width样式，方便控制长度
     *
     * 默认为固定长度200px，如果要自动长度，可以设置为auto
     */
    headWidth: {
      type: String,
      default: () => `${config.headWidth}px`,
    },
  },
  setup(props, { emit }) {
    onMounted(() => {
      updateTitle();
    });
    const name = ref(props.name || '');
    watch(
      () => props.name,
      (v) => {
        name.value = v || '';
      }
    );
    const isHove = ref(false);
    const tips = ref<HTMLElement>();
    const arrow = ref<HTMLElement>();
    const isShowTips = ref(false);
    let popperInstance: any = null;
    const emitter = new TinyEmitter();
    provide('emitter', emitter);
    function showTipsFunc(target: any) {
      const tooltip: string = toRaw(props.tooltip);
      if (tooltip && tips.value) {
        const el = toRaw(tooltipElement.value) as HTMLElement;
        if (el && (tooltip.indexOf('<br>') !== -1 || tooltip.indexOf('<br/>') !== -1)) {
          el.innerHTML = tooltip;
        }
        isShowTips.value = true;
        popperInstance = createPopper(target, tips.value, {
          placement: 'top-start',
          modifiers: [
            {
              name: 'arrow',
              options: {
                element: arrow.value,
                padding: 6, // popper带有圆角时，不希望箭头移动到圆角
              },
            },
            {
              name: 'offset',
              options: {
                offset: [5, 5],
              },
            },
          ],
        });
      }
    }
    const tooltipElement = ref<HTMLElement>();
    let timer: any = null;
    const text = ref<HTMLElement>();
    const isExpand = ref(props.expand);
    watch(
      () => props.expand,
      (val) => {
        const v = toRaw(val);
        isExpand.value = v;
        // 这种属于是外界改变导致的，外部应该自己管理好这个状态，不再触发事件，否则会导致触发2次
        // emit('changeExpand', v);
      }
    );
    let clientX: number = 0;
    const doc = uiElement.getDoc();
    function _onMouseUp(event: MouseEvent) {
      doc.removeEventListener('mousemove', _onMouseMove);
      doc.removeEventListener('mouseup', _onMouseUp);
      doc.removeEventListener('onselectstart', _onSelect);
    }
    function _onMouseMove(event: MouseEvent) {
      // 防止频繁触发
      if (Math.abs(clientX - event.clientX) < 10) {
        return;
      }
      let calcStep = Math.abs(props.step || 0);
      if (event.clientX > clientX) {
        calcStep = calcStep;
      } else {
        calcStep = -calcStep;
      }
      emit('slide', calcStep);
      clientX = event.clientX;
      emitter.emit('slide', calcStep);
    }
    function _onSelect() {
      return false;
    }
    const title = ref<string>('');
    function updateTitle() {
      if (text.value) {
        const el = text.value as HTMLElement;
        if (el.clientWidth < el.scrollWidth) {
          title.value = props.name || '';
        } else {
          title.value = '';
        }
      }
    }
    watch(
      () => props.name,
      (val) => {
        title.value = val || '';
      }
    );
    return {
      title,
      tooltipElement,
      tips,
      isShowTips,
      arrow,
      isExpand,
      text,
      name,
      isHove,
      onMouseDown(event: MouseEvent) {
        if (!props.slide) {
          return;
        }
        clientX = event.clientX;
        doc.addEventListener('mousemove', _onMouseMove);
        doc.addEventListener('mouseup', _onMouseUp);
        doc.addEventListener('onselectstart', _onSelect);
      },
      getArrowClass() {
        const cls = [];
        if (props.arrow) {
          cls.push('arrow-visible');
        } else {
          cls.push('arrow-hidden');
        }
        if (isExpand.value) {
          cls.push('iconfont icon_arrow_down');
        } else {
          cls.push('iconfont icon_arrow_right');
        }
        return cls.join(' ');
      },
      onHover(event: any) {
        if (props.tooltip) {
          clearTimeout(timer);
          timer = setTimeout(() => {
            showTipsFunc(text.value);
          }, 600);
        }
      },
      onClickArrow() {
        const v = !toRaw(isExpand.value);
        isExpand.value = v;
        emit('update:expand', v);
        emit('changeExpand', v);
      },
      onOver() {
        if (props.tooltip) {
          clearTimeout(timer);
          isShowTips.value = false;
          popperInstance?.destroy();
          popperInstance = null;
        }
      },
      getValueStyle() {
        let align = 'center';
        if (props.align === 'center') {
          align = 'center';
        } else if (props.align === 'left') {
          align = 'flex-start';
        } else if (props.align === 'right') {
          align = 'flex-end';
        }
        const css = `justify-content: ${align};`;
        return css;
      },
    };
  },
});
