
import { defineComponent, ref, reactive, watch, toRaw } from 'vue';
import { createPopper } from '@popperjs/core';
import chroma from 'chroma-js';

export default defineComponent({
  name: 'CCButton',
  props: {
    color: {
      type: String,
      default: '#4e4e4e',
    },
    tooltip: {
      type: String,
      default: '',
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  emits: ['confirm'],
  setup(props: any, { emit }) {
    const isClick = ref(false);
    const isHover = ref(false);
    const isDisabled = ref(props.disabled);
    const isShowTips = ref(false);
    const tips = ref<HTMLDivElement>();
    watch(
      () => props.disabled,
      () => {
        isDisabled.value = props.disabled;
        updateColor();
      }
    );
    let timer: any = null;
    let popperInstance: any = null;
    function showTipsFunc(target: any) {
      if (props.tooltip && tips.value) {
        isShowTips.value = true;
        popperInstance = createPopper(target, tips.value, {
          placement: 'top-start',
          modifiers: [
            {
              name: 'arrow',
              options: {
                element: arrow.value,
                padding: 6, // popper带有圆角时，不希望箭头移动到圆角
              },
            },
            {
              name: 'offset',
              options: {
                offset: [5, 5],
              },
            },
          ],
        });
      }
    }
    function calcColor() {
      if (isDisabled.value) {
        return '#969696';
      }
      return toRaw(props.color);
    }
    function updateColor() {
      theme.value.background = hexColor = calcColor();
    }
    const arrow = ref<HTMLElement>();
    const text = ref<HTMLElement>();
    const theme = ref({ background: '' });
    let hexColor = calcColor();
    updateColor();
    return {
      isDisabled,
      arrow,
      text,
      tips,
      isShowTips,
      theme,
      isClick,
      onHover(event: any) {},
      onMouseup() {
        if (isDisabled.value) {
          return;
        }
        isClick.value = false;
        theme.value.background = hexColor;
      },
      onMousedown() {
        if (isDisabled.value) {
          return;
        }
        isClick.value = true;
        theme.value.background = chroma(hexColor).darken(0.5).hex();
      },
      onClick() {
        if (isDisabled.value) {
          return;
        }
        emit('confirm');
      },
      onMouseenter() {
        if (props.tooltip) {
          clearTimeout(timer);
          timer = setTimeout(() => {
            showTipsFunc(text.value);
          }, 600);
        }
        isHover.value = true;
        theme.value.background = chroma(hexColor).brighten(0.3).hex();
      },
      onMouseleave() {
        if (props.tooltip) {
          clearTimeout(timer);
          isShowTips.value = false;
          popperInstance?.destroy();
          popperInstance = null;
        }
        isHover.value = false;
        theme.value.background = hexColor;
      },
    };
  },
});
