
import { defineComponent, ref, toRaw, watch } from 'vue';

export default defineComponent({
  name: 'CCSection',
  props: {
    name: {
      type: String,
    },
    expand: {
      type: Boolean,
      default: true,
    },
    /**
     * 鼠标移动到header时，鼠标移入显示slot header，鼠标离开隐藏slot header
     */
    autoSlotHeader: {
      type: Boolean,
      default: false,
    },
    /**
     * 是否点击整个header控制折叠，默认只会点击标题才会
     */
    expandByFullHeader: {
      type: Boolean,
      default: false,
    },
    colorTitle: {
      type: String,
      default: '#11ff00',
    },
    colorHeader: {
      type: String,
      default: '#bdbdbd',
    },
  },
  emit: ['change'],
  setup(props, { emit }) {
    const expand = ref(props.expand || false);
    const visibleSlotHeader = ref(true);
    if (props.autoSlotHeader) {
      visibleSlotHeader.value = false;
    }
    watch(
      () => props.expand,
      (v) => {
        expand.value = !!v;
      }
    );
    const name = ref(props.name || '');
    watch(
      () => props.name,
      (v) => {
        name.value = v || '';
      }
    );
    function onExpand() {
      expand.value = !expand.value;
      emit('change', toRaw(expand.value));
    }
    return {
      expand,
      visibleSlotHeader,
      name,
      getStyleHeader() {
        const css: string[] = [];
        css.push(`color:${props.colorHeader} !important`);
        return css.join(';');
      },
      getStyleTitle() {
        const css: string[] = [];
        css.push(`color:${props.colorTitle} !important`);
        return css.join(';');
      },
      onMouseEnter() {
        if (props.autoSlotHeader) {
          visibleSlotHeader.value = true;
        }
      },
      onMouseLeave() {
        if (props.autoSlotHeader) {
          visibleSlotHeader.value = false;
        }
      },
      onExpand,
      onClickHeader() {
        if (props.expandByFullHeader) {
          onExpand();
        }
      },
    };
  },
});
